import React, { Component } from 'react';
export default class Resume extends Component {
    render() {
        return (
            <React.Fragment>
                <section id="resume">
                    {/* Education */}
                    <div className="row education">
                    <div className="three columns header-col">
                        <h1><span>Education</span></h1>
                    </div>
                    <div className="nine columns main-col">
                        <div className="row item">
                        <div className="twelve columns">
                            <h3>University of Utah</h3>
                            <p className="info">Bachelor of Arts in Linguistics<span>•</span> <em className="date">December 2018</em></p>
                            <p>
                            I chose linguistics as my major because I already had acquired a lot programming experience on my own by creating various
                            games and I knew that the knowledge from linguistics would pair up well with programmming as there are a lot of concepts
                            that map well to programming in general. I focused mostly on syntax and computational linguistics for the previously stated reason.
                            </p>
                        </div>
                        </div> {/* item end */}
                        <div className="row item">
                        <div className="twelve columns">
                            <h3>Salt Lake Community College</h3>
                            <p className="info">Associates Degree in General Education <span>•</span> <em className="date">August 2017</em></p>
                            <p>
                            I studied at Salt Lake Community College to get an idea of what kind of things I'd be interested which is why I went with a degree in
                            general education. During that time I went on a study abroad trip to Japan, realized I loved the country and decided that I wanted to return
                            one day and live there for at least some time.
                            </p>
                        </div>
                        </div> {/* item end */}
                    </div> {/* main-col end */}
                    </div> {/* End Education */}
                    {/* Work */}
                    <div className="row work">
                    <div className="three columns header-col">
                        <h1><span>Work</span></h1>
                    </div>
                    <div className="nine columns main-col">
                        <div className="row item">
                        <div className="twelve columns">
                            <h3>Mire Studios</h3>
                            <p className="info">Progammer <span>•</span> <em className="date">March 2013 - Present</em></p>
                            <p>
                            This is a company I started with family so that we could have a fun way to create video games which is why I'm always "employed" here.
                            We use it as a way to just make whatever kind of game we feel like at the given time and I use it to keep up with programming by
                            trying out various techniques, technologies, and frameworks.

                            So far, we have released a few demos and a title that can currently be found on Steam and the Xbox One. I have learned how to use C#, Unity 3D,
                            and MonoGame/FNA extensively through the projects we've created through this company.
                            </p>
                        </div>
                        </div> {/* item end */}
                    </div> {/* main-col end */}
                    </div> {/* End Work */}
                    {/* Skills */}
                    <div className="row skill">
                    <div className="three columns header-col">
                        <h1><span>Skills</span></h1>
                    </div>
                    <div className="nine columns main-col">
                        <p>I've acquired a few skills during my time as a programmer and you'll find a list of some of them below. I try to keep this as up to date
                        as possible but it can be a bit difficult at times as the programming landscape is constantly evolving at an incredibley quick pace.
                        </p>
                        <div className="bars">
                        <ul className="skills">
                            <li><span className="bar-expand ninetyPercent" /><em>C# Programming</em></li>
                            <li><span className="bar-expand fiftyPercent" /><em>C Programming</em></li>
                            <li><span className="bar-expand fiftyPercent" /><em>Javascript</em></li>
                            <li><span className="bar-expand ninetyPercent" /><em>Unity 3D</em></li>
                            <li><span className="bar-expand ninetyPercent" /><em>MonoGame/FNA</em></li>
                            <li><span className="bar-expand fiftyPercent" /><em>React JS</em></li>
                        </ul>
                        </div>{/* end skill-bars */}
                    </div> {/* main-col end */}
                    </div> {/* End skills */}
                </section> {/* Resume Section End*/}
            </React.Fragment>
        );
    }
}