import React, { Component } from 'react';
export default class Portfolio extends Component {
    render() {
        const battleKnights = "Battle Knights";
        const shrines = "Shrines of Eternity";
        const slashers = "Neon Slashers";
        const duel = "Mire Duel";
        const xna = "MonoGame/FNA";
        const videoGame = "Video Game";
        const unity3D = "Unity 3D";
        const csharp = "C#";

        const portfolio = [
            { modalID: "#modal-01", title: battleKnights, type: videoGame, img: "images/portfolio/battleKnights.jpg" },
            { modalID: "#modal-02", title: shrines, type: videoGame, img: "images/portfolio/shrines.jpg" },
            { modalID: "#modal-03", title: slashers, type: videoGame, img: "images/portfolio/slashers.jpg" },
            { modalID: "#modal-04", title: duel, type: videoGame, img: "images/portfolio/mireDuel.jpg" },
        ]

        const popups = [
            { 
                id: "modal-01", 
                title: battleKnights, 
                description: "Title that was released on both Steam and Xbox One. It's a 2D platform that has a mix of modes for 1-6 players (depending on mode).",
                img: "images/portfolio/modals/m-battleKnights.jpg", 
                tags: videoGame + ", " + unity3D + ", " + csharp, 
                link: "http://www.mirestudios.com/battle-knights/" 
            },
            { 
                id: "modal-02", 
                title: shrines, 
                description: "The very first title I was part of that was released (for free). I'll always think of it fondly because of that.",
                img: "images/portfolio/modals/m-shrines.png", 
                tags: videoGame + ", " + unity3D + ", " + csharp, 
                link: "https://mire-studios.itch.io/shrines-of-eternity" 
            },
            { 
                id: "modal-03", 
                title: slashers, 
                description: "Second game that was released on Steam. It's a simple online game that taught me about networking and MonoGame/FNA.",
                img: "images/portfolio/modals/m-slashers.jpg", 
                tags: videoGame + ", " + xna + ", " + csharp, 
                link: "http://www.mirestudios.com/neon-slashers/" 
            },
            { 
                id: "modal-04", 
                title: duel, 
                description: "Another small game that involves matching buttons (combos) and trying to defeat your opponent. Features a local versus mode.",
                img: "images/portfolio/modals/m-mireDuel.jpg", 
                tags: videoGame + ", " + unity3D + ", " + csharp, 
                link: "https://mire-studios.itch.io/mire-duel" 
            },
        ];

        return (
            <React.Fragment>
                <section id="portfolio">
                    <div className="row">
                    <div className="twelve columns collapsed">
                        <h1>Below you'll find some stuff I've worked on.</h1>
                        {/* portfolio-wrapper */}
                        <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                        {portfolio.map(item => {
                            return (
                                <div className="columns portfolio-item">
                                    <div className="item-wrap">
                                    <a href={item.modalID} title>
                                        <img alt="" src={item.img} />
                                        <div className="overlay">
                                        <div className="portfolio-item-meta">
                                            <h5>{item.title}</h5>
                                            <p>{item.type} </p>
                                        </div>
                                        </div>
                                        <div className="link-icon"><i className="icon-plus" /></div>
                                    </a>
                                    </div>
                                </div>
                            )
                        })}
                        </div> {/* portfolio-wrapper end */}
                    </div> {/* twelve columns end */}
                    {/* Modal Popup
                    --------------------------------------------------------------- */}
                    {popups.map(item => {
                        return (
                            <div id={item.id} className="popup-modal mfp-hide">
                                <img className="scale-with-grid" src={item.img} alt="" />
                                <div className="description-box">
                                <h4>{item.title}</h4>
                                <p>{item.description}</p>
                                <span className="categories"><i className="fa fa-tag" />{item.tags}</span>
                                </div>
                                <div className="link-box">
                                <a href={item.link}>Details</a>
                                <a className="popup-modal-dismiss">Close</a>
                                </div>
                            </div>
                        )
                    })}
                    </div> {/* row End */}
                </section> {/* Portfolio Section End*/}
            </React.Fragment>
        );
    }
}