import React, { Component } from 'react';
export default class About extends React.Component {
    render() {
        return (
            <React.Fragment>
                <section id="about">
                    <div className="row">
                    <div className="three columns">
                    </div>
                    <div className="nine columns main-col">
                        <h2>A bit about myself</h2>
                        <p>I started programming originally because I really like playing video games and wanted to find another way to inegrate them into my life.
                            What I didn't expect was how much I'd actually enjoy programming. Nowadays, I'll take on any kind of programming challenge because I like keeping
                            my skill set up to date. I'm constantly learning new programming languages and technologies as I believe learning how they work makes me
                            a better programmer overall even if I end up not using them.
                        </p>
                    </div> {/* end .main-col */}
                    </div>
                </section> {/* About Section End*/}
            </React.Fragment>
        );
    }
}